function App() {
  return (
    <div className="App">
      <Header />
      <Router>
        <Switch>
          <Route path="/chat">
            <h1>Chat</h1>
          </Route>
          <Route path="/">
            <Cards />
          </Route>
        </Switch>
      </Router>
    </div>
  );
}

export default App;