import { Card } from '@material-ui/core';
import React, { useState } from 'react';
import TinderCard from "react-tinder-card"
import "./Cards.css"


function Cards() {
const [people, setPeople] = useState([
    {
        name: "Dav",
        url: "https://pyxis.nymag.com/v1/imgs/41d/9fb/46c48b6f0a02a5609a28c29bdef932b304-travis-scott.2x.rsquare.w330.jpg"
    }, 
    {
        name: "Koko",
        url: "https://hips.hearstapps.com/hmg-prod.s3.amazonaws.com/images/124592311-185073559820653-4515148187985309671-n-1605107838.jpg?crop=0.786xw:0.525xh;0.124xw,0.0568xh&resize=768:*"
    }
]);

    return (
        <div>
            {people.map((person) => (
                <div className= "cardContainer">
                <TinderCard
                className= "swipe"
                key= {person.name}
                preventSwipe={['up','down']}
                >
                    <div 
                    style= {{backgroundImage: `url(${person.url})`}}
                    className="card"
                    >
                        <h3>{person.name}</h3>
                    </div>
                </TinderCard>
                </div>
            ))}
        </div>
    )
}

export default Cards;